<template>
  <div class="navContainer">

    <button
      @click="btnClick"
      :class="{ nfp: !$route.meta.ifg }"
      :style="{
        background: btnColor,
        color: fontColor
      }"
      @mouseover="mouseOver"
      @mouseout="mouseOut"
    >
      Discover More
    </button>
    <transition name="fade">
      <nav v-if="isShow">
        <ul>
          <li v-for="item in nav" :key=item.text :to=item.link @click="navClick">
            <router-link :to=item.link >
              <icon :name=item.icon />
              <span>{{ item.text }}</span>
            </router-link>
          </li>
        </ul>
      </nav>
    </transition>
  </div>
</template>

<script>
import 'vue-awesome/icons/home'
import 'vue-awesome/icons/check-circle'
import 'vue-awesome/icons/graduation-cap'
import 'vue-awesome/icons/briefcase'
import 'vue-awesome/icons/user'

export default {
  name: 'Nav',
  data () {
    return {
      isShow: false,
      nav: [{icon: 'home', text: 'Home', link: '/'},
        {icon: 'check-circle', text: 'Skills', link: '/Skills'},
        {icon: 'graduation-cap', text: 'Education', link: '/Education'},
        {icon: 'briefcase', text: 'Work Exp', link: '/WorkExp'},
        {icon: 'user', text: 'Contact', link: '/Contact'}
      ],
      btnColor: this.$route.meta.color,
      fontColor: '#ecf0f1',
      borderT: 'transparent'
    }
  },
  methods: {
    mouseOver: function () {
      this.btnColor = '#ecf0f1'
      this.fontColor = this.$route.meta.color
      this.borderT = '#ecf0f1'
      this.$root.$emit('send', '#ecf0f1')
    },
    mouseOut: function () {
      this.btnColor = this.$route.meta.color
      this.borderT = 'transparent'
      this.fontColor = '#ecf0f1'
      this.$root.$emit('send', this.$route.meta.color)
    },
    btnClick: function () {
      this.isShow = true
      if (this.isShow === true) {
        this.$root.$emit('btnclick', true)
      }
    },
    navClick: function () {
      this.isShow = false
      this.$root.$emit('btnclick', false)
    }
  }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/main

.fade-enter-active,
.fade-leave-active
  transition: all .3s ease
.fade-enter,
.fade-leave-to
  transform: scale(0)
  opacity: 0
.navContainer
  button
    text-align: center
    display: block
    color: $white
    transition: all 0.6s ease
    border: 1px solid $white
    outline: none
    padding: 0.8rem 2rem
    cursor: pointer
    margin: 2.5rem auto 0
    transition: all 0.6s ease
    +bp(min, 768px)
      margin-left: 0

  button.nfp
    position: absolute
    bottom: -1px
    border-bottom: none
    left: 50%
    transform: translateX(-50%)

  nav
    background: rgba(0,0,0,.87)
    position: absolute
    +trbl(0px, 0px, 0px, 0px)
    -webkit-box-shadow: 0px 0px 0px 1px rgba(0,0,0,0.87)
    -moz-box-shadow: 0px 0px 0px 1px rgba(0,0,0,0.87)
    box-shadow: 0px 0px 0px 1px rgba(0,0,0,0.87)
    ul
      margin: 0
      padding: 0
      height: 100%
      list-style: none
      padding-left: 0
      display: flex
      flex-direction: column
      justify-content: space-around
      align-items: center
      li
        display: block
        width: 8rem
        padding: 5px 0 5px 0
        border-bottom: 1px solid #eee
        // &:hover
        //   background: $white
        //   a
        //     color: $home
        a
          color: $white
          text-decoration: none
          display: flex
          flex-direction: row
          justify-content: center
          align-items: center

          svg
            margin-right: .4rem

</style>



