<template>
  <div id="app" :style="{ background: $route.meta.color }">
    <router-view
      class="view"
    />
  </div>
</template>

<script>

export default {
  name: 'App'
}
</script>

<style lang="sass">
@import ./assets/sass/main

*
  font-family: 'Roboto', sans-serif

#app
  +size(100vw, 100vh)
  transition: all .6s ease
  .view
    background: transparent
    position: absolute
    +trbl(10px, 10px, 10px, 10px)
    display: flex
    justify-content: center
    align-items: center
    border: 1px solid $white
    outline: 10px solid
    box-sizing: border-box
    transition: all .6s ease
    overflow: none
</style>



