<template>
  <div class="container" :style="{ outlineColor: outlineColor }">
    <div class="homeview">
      <section class="hi" :class="{ 'bgblur': navOpen}">
        Hi<span>!</span>
      </section>
      <section class="welcome">
        <div :class="{ 'bgblur': navOpen}">
          <h1>I am Khee</h1>
          <h2>&</h2>
          <h1>I make Websites</h1>
        </div>
        <Nav />
      </section>

    </div>
  </div>
</template>

<script>
import AboutMe from './AboutMe.vue'
import Close from './Close.vue'
import Nav from './Nav.vue'

export default {
  name: 'Home',
  data () {
    return {
      outlineColor: this.$route.meta.color,
      navOpen: false
    }
  },
  created () {
    this.$root.$on('send', (data) => {
      this.outlineColor = data
    })
    this.$root.$on('btnclick', (data) => {
      this.navOpen = data
    })
  },
  components: {AboutMe, Close, Nav}
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/main

.container
  transition: all .6s ease
  color: $white
  .homeview
    +bp(min, 768px)
      display: flex
      align-items: flex-start
      flex-wrap: wrap
    .hi
      font-size: 5rem
      box-sizing: border-box
      +bp(min, 768px)
        padding-top: rem-calc(15px)
        padding-right: rem-calc(40px)
      text-align: center
      span
        display: none
        +bp(min, 768px)
          display: inline
    .welcome
      transition: all 0.6s ease
      text-align: center
      h1, h2
        font-family: 'Roboto Slab', serif
        +bp(min, 768px)
          text-align: left
</style>



